/* JupyterLab-like UI for ClipboardPage */
.clipboard-jupyter-root {
  font-family: 'Segoe UI', 'Arial', 'sans-serif';
  background: #f5f6fa;
  min-height: 100vh;
  padding-bottom: 24px;
  width: 100%;
}
.stealth-mode .jupyter-header,
.stealth-mode .jupyter-toolbar-thin,
.stealth-mode .jupyter-menu-tabs,
.stealth-mode .jupyter-notebook-area {
  display: none !important;
}

/* Hide stealth mode UI elements */
.jupyter-stealth-toggle {
  display: none !important;
}
.stealth-indicator {
  display: none !important;
}

.jupyter-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: white;
  border-bottom: 1px solid #e0e0e0;
  padding: 0;
  font-size: 15px;
  font-weight: 500;
  height: 55px;
  min-height: 42px;
  box-shadow: 0 1px 0 #e0e0e0;
  width: 100%;
}
.jupyter-header-content {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 14px;
}
.jupyter-logo-title {
  display: flex;
  align-items: center;
  gap: 7px;
}
.jupyter-logo {
  height: 33px;
  margin-right: 5px;
}
.jupyter-title {
  font-size: 25px;
  font-weight: 600;
  color: #444;
  margin-left: 2px;
}
.jupyter-header .jupyter-status {
  margin-left: 12px;
  color: #888;
  font-size: 11.5px;
  font-weight: 400;
  position: static;
}
.jupyter-header .jupyter-kernel-info {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 7px;
  font-size: 12.5px;
  color: #444;
}
.jupyter-kernel-status {
  color: #4caf50;
  border: 1px solid #4caf50;
  border-radius: 3px;
  padding: 2px 6px;
  margin-left: 9px;
}
.jupyter-kernel-type {
  color: #555;
  background: #eee;
  border-radius: 3px;
  padding: 2px 7px;
}
.jupyter-logout {
  background: #f44336;
  color: #fff;
  border: none;
  border-radius: 3px;
  padding: 4px 11px;
  margin-left: 13px;
  cursor: pointer;
  font-size: 14px;
}
.jupyter-toolbar {
  display: flex;
  align-items: center;
  gap: 5px;
  background: #f7f7f7;
  border-bottom: 1px solid #e0e0e0;
  padding: 7px 18px;
}
.jupyter-toolbar button {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 3px 6px;
  cursor: pointer;
  font-size: 15px;
  margin-right: 2px;
}
.jupyter-toolbar button:hover {
  background: #e0e0e0;
}
.jupyter-toolbar select {
  margin-left: 9px;
  padding: 2px 6px;
  border-radius: 3px;
  border: 1px solid #ccc;
}
.jupyter-notebook-area {
  background: #f5f6fa;
  max-width: 1000px;
  margin: 24px auto 0 auto;
  padding: 0;
  border-radius: 0;
  box-shadow: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.jupyter-cell {
  width: 100%;
  max-width: 1000px;
  margin: 0 0 12px 0;
  background: transparent;
  border: none;
  box-shadow: none;
  border-radius: 0;
  padding: 0;
  position: relative;
  display: flex;
  justify-content: center;
}
.jupyter-cell-row {
  display: flex;
  align-items: stretch;
  padding: 0;
  min-height: 32px;
  position: relative;
  width: 100%;
}
.jupyter-cell-prompt {
  width: 50px;
  min-width: 50px;
  color: #1976d2;
  font-family: 'Consolas', 'Menlo', 'Monaco', monospace;
  font-size: 11.5px;
  padding: 8px 0 0 0;
  text-align: right;
  user-select: none;
  font-weight: 600;
  background: transparent;
  line-height: 1.2;
}
.jupyter-cell-content {
  flex: 1;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.jupyter-markdown-row {
  width: 100%;
  text-align: left;
  margin-bottom: 20px;
  max-width: 1000px;
}
.jupyter-markdown-content {
  text-align: left;
  width: 100%;
}
.jupyter-markdown-content h1,
.jupyter-markdown-content h2,
.jupyter-markdown-content h3,
.jupyter-markdown-content p {
  text-align: left !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
  padding-left: 0 !important;
}
.jupyter-markdown-content h1,
.jupyter-markdown-content h2,
.jupyter-markdown-content h3 {
  font-weight: 700;
  color: #222;
  margin: 0 0 10px 0;
  text-align: left;
}
.jupyter-markdown-content h1 {
  font-size: 1.8rem !important;
  font-weight: 800 !important;
  margin-bottom: 16px !important;
  color: #2c3e50 !important;
}
.jupyter-markdown-content h2 {
  font-size: 1.4rem !important;
  font-weight: 700 !important;
}
.jupyter-markdown-content h3 { font-size: 1.08rem; }
.jupyter-code-content {
  background: none;
  border-radius: 4px;
  font-family: 'Menlo', 'Consolas', monospace;
  font-size: 12.5px;
  color: #222;
  padding: 8px 12px 8px 4px;
  margin: 0;
  overflow-x: auto;
  min-height: 22px;
  border: none;
  box-shadow: none;
  line-height: 1.35;
  width: 100%;
}
.jupyter-output-row {
  background: #f9fbe7;
  border-top: 1px solid #e0e0e0;
}
.output-prompt {
  color: #388e3c;
}
.jupyter-output-content {
  color: #444;
  font-family: 'Consolas', 'Menlo', 'Monaco', monospace;
  font-size: 15px;
  padding: 10px 10px 10px 0;
}
.jupyter-toolbar-thin {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
  background: white;
  width: 100%;
  border-bottom: 1px solid #e5e5e5;
  padding: 8px 0;
  height: 40px;
}
.jupyter-toolbar-content {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  padding: 0 14px;
}
.jupyter-toolbar-thin button {
  background: none;
  border: none;
  padding: 4px 7px;
  border-radius: 3px;
  cursor: pointer;
  font-size: 15px;
  color: #444;
  transition: background 0.15s;
  margin-right: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.jupyter-toolbar-thin button i {
  font-size: 13px;
}
.jupyter-toolbar-thin button:hover {
  background: #eee;
}
.jupyter-toolbar-thin select.jupyter-celltype {
  margin-left: 11px;
  padding: 3px 7px;
  border-radius: 3px;
  border: 1px solid #ddd;
  background: #fff;
  font-size: 13px;
  color: #444;
}
.jupyter-toolbar-thin .jupyter-toolbar-right {
  margin-left: auto;
  color: #444;
  font-size: 12px;
  display: flex;
  align-items: center;
}
.jupyter-toolbar-thin .jupyter-kernel-logo {
  margin-left: 4px;
  height: 22px;
}
.jp-bluebar {
  width: 5px;
  min-width: 5px;
  background: transparent;
  margin-right: 4px;
  transition: background 0.15s;
}
.jupyter-cell-row:hover .jp-bluebar {
  background: #1976d2;
}
.jupyter-kernel-info {
  display: flex;
  align-items: center;
  gap: 6px;
}
.jupyter-kernel-logo {
  height: 24px;
}
.jp-cell-controls-wrapper {
  position: absolute;
  right: 10px;
  top: 5px;
  display: flex;
  align-items: center;
  opacity: 1;
  z-index: 20;
  pointer-events: auto;
}
.jp-cell-controls {
  display: flex;
  align-items: center;
  gap: 4.75px;
  background: rgba(255, 255, 255, 0.95);
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid #ddd;
  pointer-events: auto;
}
.jp-cell-controls button:not(:first-child) {
  margin-left: 1.9px;
}
.jp-cell-controls button {
  background: none;
  border: none;
  padding: 2px 5px;
  cursor: pointer;
  border-radius: 3px;
  color: #444;
  font-size: 13px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  transition: all 0.15s;
  pointer-events: auto;
}
.jp-cell-controls button:hover {
  background: #e9e9e9;
  color: #222;
}
.jp-cell-controls i {
  font-size: 12px;
}
.jp-markdown-blend {
  border-radius: 4px;
  padding: 10px;
  transition: background 0.15s;
  position: relative;
}
.jp-markdown-blend .jupyter-cell-content {
  border-left: 3px solid transparent;
  padding-left: 10px;
}
.jp-markdown-blend:focus-within {
  background: rgba(200, 200, 200, 0.05);
}
.jupyter-cell,
.jupyter-markdown-row {
  max-width: 1000px;
  width: 100%;
  margin-bottom: 12px;
}
.markdown-left-align {
  text-align: left !important;
  width: 100%;
}
.markdown-left-align h1,
.markdown-left-align h2,
.markdown-left-align h3,
.markdown-left-align h4,
.markdown-left-align h5,
.markdown-left-align h6,
.markdown-left-align p,
.markdown-left-align ul,
.markdown-left-align ol {
  text-align: left !important;
  width: 100%;
  max-width: 100%;
}
.markdown-left-align h2 {
  font-size: 1.3rem;
  margin-top: 10px;
  margin-bottom: 15px;
  color: #333;
  font-weight: 600;
  border-bottom: none;
}

/* Enhanced image loading for Python icons */
.jupyter-kernel-logo, .jupyter-logo {
  display: inline-block !important;
  visibility: visible !important;
  opacity: 1 !important;
}

/* Basic sizing for images */
.jupyter-kernel-logo {
  height: auto;
}

.jupyter-logo {
  height: auto;
}

/* Remove problematic preloader */

.jp-in-prompt {
  color: #1976d2 !important;
  font-weight: 600;
}
.jp-out-prompt {
  color: #b20000 !important;
  font-weight: bold;
}
.jp-code-block-wrapper {
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.06);
  border: 1px solid #e0e0e0;
  position: relative;
  width: 1000px;
  min-width: 1000px;
  max-width: 1000px;
  margin: 0;
  display: flex;
  flex-direction: row;
  position: relative;
}
.jp-copy-btn {
  position: absolute;
  right: 8px;
  top: 8px;
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 4px 8px;
  cursor: pointer;
  opacity: 1;
  z-index: 10;
  font-size: 12px;
  pointer-events: auto;
  color: #444;
}
.jp-keyword {
  color: #0033B3;
}
.jp-string {
  color: #067D17;
}
.jp-comment {
  color: #8C8C8C;
}
.jp-number {
  color: #1750EB;
}
.jupyter-menu-tabs {
  background: white;
  width: 100%;
  border-bottom: 1px solid #eaeaea;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 36px;
  padding: 0;
}
