  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  padding: 0 14px;
}
.jupyter-menu-tabs span {
  padding: 7px 13px;
  font-size: 13px;
  color: #444;
  cursor: pointer;
  font-weight: 500;
  border-bottom: 2px solid transparent;
  transition: all 0.15s;
}
.jupyter-menu-tabs span:hover {
  border-bottom: 2px solid #1976d2;
}
