/* Tony Stark-inspired UI - Productivity Page */
.productivity-page {
  background-color: #0a0a0a;
  min-height: 100vh;
  font-family: 'Consolas', 'Courier New', monospace;
  color: #00ffff;
  padding-top: 80px;
  position: relative;
  overflow: hidden;
}

/* Subtle grid pattern background */
.productivity-page::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.02) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 255, 0.02) 1px, transparent 1px);
  background-size: 50px 50px;
  z-index: 0;
}

/* Scanning line effect */
.productivity-page::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(to right, transparent, #0ff, transparent);
  opacity: 0.5;
  z-index: 100;
  animation: scanLine 10s linear infinite;
}

@keyframes scanLine {
  0% {
    top: -2px;
  }
  100% {
    top: 100%;
  }
}

.productivity-dashboard {
  max-width: 1400px;
  margin: 0 auto;
  padding: 1rem;
  position: relative;
  z-index: 1;
}

.dashboard-header {
  margin-bottom: 1.5rem;
  text-align: center;
  position: relative;
}

.dashboard-header h1 {
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 0.5rem;
  color: #00ffff;
  letter-spacing: 2px;
  text-transform: uppercase;
  position: relative;
}

.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 1.5rem;
  height: calc(100vh - 180px);
  min-height: 700px;
  position: relative;
}

/* Translucent glass effect for cards */
.dashboard-card {
  background-color: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 255, 255, 0.2);
  overflow: hidden;
  transition: border-color 0.3s ease;
  display: flex;
  flex-direction: column;
  position: relative;
  box-shadow: inset 0 0 20px rgba(0, 255, 255, 0.1);
}

.dashboard-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid transparent;
  border-image: linear-gradient(135deg, transparent, rgba(0, 255, 255, 0.3) 20%, rgba(0, 255, 255, 0.3) 80%, transparent);
  border-image-slice: 1;
  pointer-events: none;
  opacity: 0.5;
  z-index: 0;
}

.dashboard-card:hover {
  border-color: rgba(0, 255, 255, 0.4);
}

/* Card headers */
.todo-header,
.journal-header,
.deadlines-header,
.personal-assistant-header {
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  border-bottom: 1px solid rgba(0, 255, 255, 0.2);
  background-color: rgba(0, 10, 15, 0.8);
  position: relative;
}

.todo-header h2,
.journal-header h2,
.deadlines-header h2,
.personal-assistant-header h2 {
  margin: 0;
  color: #00ffff;
  font-size: 0.95rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 2px;
  flex: 1;
}

/* Common Component Styles */
.todo-container,
.deadlines-container,
.journal-container,
.personal-assistant-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
  position: relative;
  z-index: 2;
}

/* Todo List Styles */
.todo-input-container {
  display: flex;
  padding: 0.75rem;
  gap: 0.75rem;
  background-color: rgba(0, 10, 15, 0.5);
  border-bottom: 1px solid rgba(0, 255, 255, 0.1);
}

.todo-input {
  flex: 1;
  padding: 0.5rem 0.75rem;
  border: 1px solid rgba(0, 255, 255, 0.2);
  font-size: 0.9rem;
  background-color: rgba(0, 0, 0, 0.7);
  color: #00ffff;
  font-family: 'Consolas', 'Courier New', monospace;
}

.todo-input:focus {
  outline: none;
  border-color: rgba(0, 255, 255, 0.5);
}

.todo-priority-select {
  width: 100px;
  padding: 0.5rem;
  border: 1px solid rgba(0, 255, 255, 0.2);
  font-size: 0.9rem;
  background-color: rgba(0, 0, 0, 0.7);
  color: #00ffff;
  font-family: 'Consolas', 'Courier New', monospace;
}

.add-todo-btn,
.add-deadline-btn,
.journal-save-btn {
  padding: 0.5rem 0.75rem;
  background-color: rgba(0, 40, 50, 0.8);
  color: #00ffff;
  border: 1px solid rgba(0, 255, 255, 0.3);
  font-size: 0.9rem;
  font-weight: 400;
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: 'Consolas', 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.add-todo-btn:hover,
.add-deadline-btn:hover,
.journal-save-btn:hover {
  background-color: rgba(0, 60, 70, 0.8);
  border-color: rgba(0, 255, 255, 0.5);
}

.todo-list-wrapper,
.deadlines-list-wrapper {
  flex: 1;
  overflow-y: auto;
  padding: 0.5rem;
  background-color: rgba(0, 0, 0, 0.5);
}

.todo-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.todo-item {
  display: flex;
  align-items: center;
  padding: 0.6rem;
  margin-bottom: 0.5rem;
  background-color: rgba(0, 10, 15, 0.6);
  border-left: 2px solid rgba(0, 255, 255, 0.3);
  transition: all 0.2s ease;
}

.todo-item:hover {
  background-color: rgba(0, 15, 20, 0.8);
  border-left-color: rgba(0, 255, 255, 0.5);
}

.todo-item.completed-task {
  background-color: rgba(0, 5, 10, 0.4);
  opacity: 0.5;
  border-left-color: rgba(0, 255, 255, 0.1);
}

.todo-checkbox-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.75rem;
}

.todo-checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.todo-checkbox-label {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  border: 1px solid rgba(0, 255, 255, 0.4);
  cursor: pointer;
  transition: all 0.2s ease;
}

.todo-checkbox:checked + .todo-checkbox-label {
  background-color: rgba(0, 255, 255, 0.2);
  border-color: rgba(0, 255, 255, 0.6);
}

.check-icon {
  color: #00ffff;
}

.todo-text {
  flex: 1;
  font-size: 0.9rem;
  color: #00ffff;
}

.todo-text.completed {
  text-decoration: line-through;
  color: rgba(0, 255, 255, 0.4);
}

.todo-priority {
  padding: 0.15rem 0.4rem;
  font-size: 0.75rem;
  margin-right: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.priority-high {
  background-color: rgba(255, 0, 0, 0.15);
  color: #ff6666;
  border: 1px solid rgba(255, 0, 0, 0.3);
}

.priority-medium {
  background-color: rgba(255, 165, 0, 0.15);
  color: #ffcc00;
  border: 1px solid rgba(255, 165, 0, 0.3);
}

.priority-low {
  background-color: rgba(0, 255, 255, 0.15);
  color: #00ffff;
  border: 1px solid rgba(0, 255, 255, 0.3);
}

/* Empty states */
.empty-state {
  color: rgba(0, 255, 255, 0.4);
  text-align: center;
  font-style: italic;
  padding: 2rem;
  letter-spacing: 1px;
}

/* Deadlines styles */
.deadline-input-container {
  display: flex;
  padding: 0.75rem;
  gap: 0.75rem;
  background-color: rgba(0, 10, 15, 0.5);
  border-bottom: 1px solid rgba(0, 255, 255, 0.1);
}

.deadline-input,
.deadline-date {
  padding: 0.5rem 0.75rem;
  border: 1px solid rgba(0, 255, 255, 0.2);
  font-size: 0.9rem;
  background-color: rgba(0, 0, 0, 0.7);
  color: #00ffff;
  font-family: 'Consolas', 'Courier New', monospace;
}

.deadline-input {
  flex: 1;
}

.deadline-date {
  width: 180px;
}

.deadline-input:focus,
.deadline-date:focus {
  outline: none;
  border-color: rgba(0, 255, 255, 0.5);
}

.deadline-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem;
  margin-bottom: 0.5rem;
  background-color: rgba(0, 10, 15, 0.6);
  border-left: 2px solid rgba(0, 255, 255, 0.3);
}

.deadline-item:hover {
  background-color: rgba(0, 15, 20, 0.8);
  border-left-color: rgba(0, 255, 255, 0.5);
}

.deadline-info {
  flex: 1;
}

.deadline-title {
  font-size: 0.95rem;
  color: #00ffff;
  margin-bottom: 0.3rem;
  letter-spacing: 0.5px;
}

.deadline-date-display {
  font-size: 0.8rem;
  color: rgba(0, 255, 255, 0.6);
}

.deadline-countdown {
  font-size: 0.8rem;
  padding: 0.15rem 0.4rem;
  border: 1px solid rgba(0, 255, 255, 0.3);
}

.deadline-approaching {
  background-color: rgba(255, 0, 0, 0.15);
  color: #ff6666;
  border-color: rgba(255, 0, 0, 0.3);
}

.deadline-upcoming {
  background-color: rgba(255, 165, 0, 0.15);
  color: #ffcc00;
  border-color: rgba(255, 165, 0, 0.3);
}

.deadline-distant {
  background-color: rgba(0, 255, 255, 0.15);
  color: #00ffff;
}

/* Journal styles */
.journal-container textarea {
  flex: 1;
  resize: none;
  background-color: rgba(0, 0, 0, 0.7);
  color: #00ffff;
  border: 1px solid rgba(0, 255, 255, 0.2);
  padding: 0.75rem;
  font-family: 'Consolas', 'Courier New', monospace;
  font-size: 0.9rem;
  outline: none;
}

.journal-container textarea:focus {
  border-color: rgba(0, 255, 255, 0.5);
}

.journal-form {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 0.75rem;
  gap: 0.75rem;
  background-color: rgba(0, 0, 0, 0.5);
}

.journal-date-picker {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  border: 1px solid rgba(0, 255, 255, 0.2);
  background-color: rgba(0, 0, 0, 0.7);
  color: #00ffff;
}

.journal-date-picker input {
  border: none;
  flex: 1;
  font-size: 0.9rem;
  outline: none;
  background-color: transparent;
  color: #00ffff;
  font-family: 'Consolas', 'Courier New', monospace;
}

.mood-selector {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.mood-selector label {
  font-size: 0.9rem;
  color: #00ffff;
  font-weight: 400;
}

.mood-selector select {
  flex: 1;
  padding: 0.5rem 0.75rem;
  border: 1px solid rgba(0, 255, 255, 0.2);
  font-size: 0.9rem;
  background-color: rgba(0, 0, 0, 0.7);
  color: #00ffff;
  font-family: 'Consolas', 'Courier New', monospace;
}

.mood-selector select:focus {
  outline: none;
  border-color: rgba(0, 255, 255, 0.5);
}

.journal-view-toggle {
  display: flex;
  margin-left: auto;
}

.journal-view-btn {
  background-color: rgba(0, 10, 15, 0.7);
  color: #00ffff;
  border: 1px solid rgba(0, 255, 255, 0.2);
  padding: 0.3rem 0.6rem;
  cursor: pointer;
  font-family: 'Consolas', 'Courier New', monospace;
  font-size: 0.8rem;
  transition: all 0.2s ease;
}

.journal-view-btn.active {
  background-color: rgba(0, 40, 50, 0.8);
  border-color: rgba(0, 255, 255, 0.4);
}

/* AI Assistant Styles */
.personal-assistant-header {
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  background-color: rgba(0, 10, 15, 0.8);
  border-bottom: 1px solid rgba(0, 255, 255, 0.15);
}

.assistant-avatar {
  width: 28px;
  height: 28px;
  background-color: rgba(0, 255, 255, 0.1);
  border: 1px solid rgba(0, 255, 255, 0.3);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #00ffff;
  margin-right: 0.75rem;
  font-size: 0.9rem;
}

.assistant-info h3 {
  margin: 0;
  font-size: 0.9rem;
  color: #00ffff;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.assistant-info p {
  margin: 0;
  font-size: 0.75rem;
  color: rgba(0, 255, 255, 0.6);
}

.clear-chat-btn {
  margin-left: auto;
  background-color: rgba(30, 0, 0, 0.3);
  border: 1px solid rgba(255, 100, 100, 0.2);
  color: rgba(255, 100, 100, 0.7);
  cursor: pointer;
  padding: 0.25rem 0.5rem;
  font-size: 0.8rem;
  transition: all 0.2s ease;
}

.clear-chat-btn:hover {
  background-color: rgba(50, 0, 0, 0.4);
  border-color: rgba(255, 100, 100, 0.4);
}

.assistant-messages {
  flex: 1;
  overflow-y: auto;
  padding: 0.75rem;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.message {
  max-width: 85%;
}

.message.user-message {
  align-self: flex-end;
}

.message.assistant-message {
  align-self: flex-start;
}

.message-bubble {
  padding: 0.65rem 0.75rem;
  font-size: 0.9rem;
  line-height: 1.4;
  word-break: break-word;
}

.user-message .message-bubble {
  background-color: rgba(0, 40, 50, 0.5);
  color: #00ffff;
  border: 1px solid rgba(0, 255, 255, 0.2);
}

.assistant-message .message-bubble {
  background-color: rgba(0, 15, 25, 0.5);
  color: #00ffff;
  border: 1px solid rgba(0, 255, 255, 0.2);
}

.assistant-input {
  display: flex;
  padding: 0.75rem;
  border-top: 1px solid rgba(0, 255, 255, 0.15);
  background-color: rgba(0, 10, 15, 0.5);
  gap: 0.75rem;
}

.assistant-input textarea {
  flex: 1;
  padding: 0.6rem 0.75rem;
  border: 1px solid rgba(0, 255, 255, 0.2);
  font-size: 0.9rem;
  resize: none;
  max-height: 100px;
  outline: none;
  background-color: rgba(0, 0, 0, 0.7);
  color: #00ffff;
  font-family: 'Consolas', 'Courier New', monospace;
}

.assistant-input textarea:focus {
  border-color: rgba(0, 255, 255, 0.5);
}

.assistant-input button {
  width: 32px;
  height: 32px;
  border: 1px solid rgba(0, 255, 255, 0.3);
  background-color: rgba(0, 40, 50, 0.8);
  color: #00ffff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  flex-shrink: 0;
}

.assistant-input button:hover {
  background-color: rgba(0, 60, 70, 0.8);
  border-color: rgba(0, 255, 255, 0.5);
}

/* Edit Mode Toggle Button - more hidden */
.edit-toggle-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 200;
}

.edit-toggle-btn {
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 255, 255, 0.1);
  color: rgba(0, 255, 255, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  opacity: 0.2;
}

.edit-toggle-btn:hover {
  opacity: 0.7;
  background-color: rgba(0, 20, 25, 0.5);
  border-color: rgba(0, 255, 255, 0.3);
}

.edit-toggle-btn.active {
  opacity: 0.5;
  background-color: rgba(0, 40, 50, 0.7);
  border-color: rgba(0, 255, 255, 0.3);
}

.edit-toggle-btn svg {
  width: 14px;
  height: 14px;
}

/* Read-only indicator */
.read-only-indicator {
  display: flex;
  align-items: center;
  padding: 3px 6px;
  background-color: rgba(20, 0, 0, 0.5);
  border: 1px solid rgba(255, 100, 100, 0.3);
  color: rgba(255, 100, 100, 0.7);
  font-size: 0.7rem;
  font-weight: 400;
  letter-spacing: 0.5px;
}

.read-only-indicator svg {
  margin-right: 4px;
  font-size: 0.7rem;
}

/* Scroll styling */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: rgba(0, 10, 15, 0.2);
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 255, 255, 0.2);
  border: 1px solid rgba(0, 255, 255, 0.1);
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 255, 255, 0.3);
}

/* Add custom styling for inputs and form elements */
input, textarea, select, button {
  outline: none !important;
} 